#wrap {
    width: 100%;
    overflow: hidden;
    #header {
        width: 100%;
        height: 140px;
        background: rgb(6,44,94);
        position: relative;
        padding: 30px 0 0 0;
        .row1 {
            width:100%;
            height: 36.391px;
            position: relative;
            .container {
                width:100%;
                max-width: 1280px;
                margin:0 auto;
                height: 100%;
                .gap {
                    height: 100%;
                    .content {
                        height: 100%;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        .left  {       
                            width: 100%;                    
