                                display: flex;
                                li {
                                    margin: 0 15px 0 0;
                                    a {
                                        font-size: 14px;
                                        color: rgba(255,255,255, 0.5);
                                        overflow: hidden;
                                        transition: all 0.3s;
                                        &::after {
                                            content: '';
                                            margin: 6px 0 0 0;
                                            display: block;
