                                            background-color: rgb(255,255,255);
                                        }
                                        }
                                       
                                    }
                                }
                            }
                        }
                        .center {
                            width: 100%;
                           h1 {
                            a {
                                
                            }
                           }     
                        }
                        .right {
                            display: flex;
                            justify-content: flex-end;
                            height: 100%;
                            align-items: center;
                            ul {
                                display: flex;
                                li {
                                    margin:0 0 0 15px;
                                    a {
                                        font-size: 14px;
                                        color: rgb(68,68,68);
                                        img {
                                            width: auto;
                                            height: 15px;
                                        }
                                    }
                                }
                            }
                            span {
                                margin:0 0 0 20px;
                                color: rgba(255, 255,255, 0.8);
                                font-size: 15px;
                            }
                        }
                    }
                }
            }
        }        
        .row2 {
            position: absolute;
            bottom: 0;
            left:0;
            width:100%;
            height: 60px;
            .container {
                width:100%;
                max-width: 1280px;
                margin:0 auto;
                height: 100%;
                .gap {
                    height: 100%;
                    .content {
                        height: 100%;
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
