#main {
    z-index: 2;
    position: relative;
    width: 100%;
    #section1 {
        width: 100%;
        .container {
            width: 100%; 
            position: relative;
            .slide-container {
                position: relative;
                z-index: 1;
                width: 100%;
                .slide-view {
                    z-index: 1;
                    position:relative;
                    width: 100%;
                    overflow: hidden;
                    .slide-wrap {
                        pointer-events: none;
                        width: 100%;
                        height: 100vh;
                        display: flex;
                        position: relative;
                       .slide {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100vh;
                        background: url() no-repeat 50% 50%;
                        background-size: cover;
                        user-select: none;
                        position : absolute;
                        z-index: 2;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100vh;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        text-align: center;
                        div {
                            width: 100%;
                            max-width: 1280px;
                            transition: all 0.5s;
                            h2 {
                                display: inline-block;
                                color: rgb(255,255,255);
                                font-size: 18px;
                                position: relative;
                                padding : 0 0 50px 0;
                                &::before {
                                    content:'';
                                    position: absolute;
                                    left: -100px;
                                    top: 15%;
                                    margin: -0.5px 0 0 0;
                                    width: 80px;
                                    height: 1px;
                                    background: rgba(255,255,255,0.5);
                                    
                                    
                                }
                                &::after {
                                    content: '';
                                    position: absolute;
                                    right:-100px;
                                    top: 15%;
                                    margin: -0.5px 0 0 0;
                                    width: 80px;
                                    height: 1px; 
                                    background: rgba(255,255,255,0.5);
                                }
                                
                            }
                            br {width: 100%;}
                            h1 {
                                font-size: 60px;
                                font-weight: 400;
                                color: rgb(255,255,255);
                                padding: 0 0 35px 0;
                            }
                            h3 {
                                font-size: 18px;
                                letter-spacing: -0.3;
                                line-height: 28.8px;
                                color: rgb(255,255,255);
                                padding: 0 0 20px 0;
                            }
                           
                             .button{
                                 background-color: rgba( 255, 255, 255, 0.1 );
                                 border: 1px solid #ccc;
                                 border-color: rgba(204,204,204,.5);
                                 letter-spacing: 0px;
                                 padding: 10px 20px;
                                 display: inline-block;
                                 margin: 3px 10px;
                                 transition: all 0.5s;   
                                 border-radius: 20px;
                                 
                             
                                 &:hover{
                                   background: rgba(96,96,0,0.5);
                                   color: white;
                                   transition: all 0.5s;
                                 }
                                 &:on {
                                    color: #626262;
                                    transition: all 0.2s;
                                 }
                                 p {
                                    color:rgb(255,255,255);
                                    
                                   
                                    
                                 }
                             }  
                            
                            
                        }
                           


                        &.slide1{
                            background-image: url(../img/main_1920_01.jpg);
                            z-index: 3;
                        }
                        &.slide2 {
                            background-image: url(../img/main_1920_02.jpg);
                            z-index: 2;
                        }
                        &.slide3 {
                            background-image: url(../img/main_1920_03.jpg);
                            z-index: 1;
                        }
                       } 
                       
                    }
                   
                }
            }
            .title {}
            
            // 좌우화살버튼
            .arrow-next-btn {
                z-index: 3;
                position :absolute;
                top : 50%;
                right: 6%;
                width: 20px;
                height: 20px;
                margin: -50px 0 0 0;
                border-radius: 100%;
               
                i {
                    display: block;
                    width: 20px;
                    height: 20px;
                    border-top: 1px solid #fff;
                    border-right: 1px solid #fff;
                    transform: rotate(45deg);
                }
            }
            .arrow-prev-btn {
                z-index: 3;
                position: absolute;
                top: 50%;
                left: 6%;
                width: 20px;
                height: 20px;
                margin: -50px 0 0 0;
                i {
                    display: block;
                    width: 100%;
                    height: 100%;
                    border-top: 1px solid #fff;
                    border-left: 1px solid #fff;
                    transform:rotate(-45deg);
                }
                
            }
        
            // 페이지버튼
            .page-btn-box {
                z-index: 2;
                position: absolute;
                bottom: 130px 0;
                left: 0;
                width: 100%;
                height: 76vh;
                margin: 0 auto;
                display: flex;
                justify-content: center;
                
                span {
                    height: 83px;
                    margin: 37px 137px; 
                    position: relative;
                   
                    a { 
                        font-size: 20px;
                        transition: all 0.3s;
                        border-bottom: 1px solid #000;
                        &::after {
                            content: '';
                            margin: 6px 0 0 0;
                            display: block;
                            width: 100%;
                            height: 2px;
                            background: rgba(34, 131, 212, 0); // 투명색상
                            transition: all 0.3s;
                        }
                        &:hover {
                            color: #ccc;
                        }
                        &.on {
                            color: #000;
                        }
                        &:hover::after {
                            background-color: rgb(255,255,255);
                        }
                        &:on::after {
                            background-color: rgb(255,255,255);
                        }
                    }
                }
            }
        }
    }

    #section2 {
        width: 100%;
        height: 644px;
        background: rgb(255,255,255);
        .container {
           position: relative;
           width: 100%;
           height: 100%;
           max-width: 1154.780px;
           margin: 0 auto;
           &.on {
            animation: sec2Ani1 1s ease-in-out both;
            }
            .gap {
                width: 100%;
                .title {
                    width: 100%; 
                    height: 100%;
                    padding: 100px 20px 0 10px;
                    &.on {
                        animation: sec2Ani1 1.3s ease-in-out both;
                    }
                    h1 {
                        display: flex;
                        font-size: 32px;
                        
                        font-weight: 400;
                        margin: 70px 0 30px 0;
                     
                    }
                }
                .content {
                    width: 100%; 
                    ul {
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        li {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            .col-gap {
                                width: auto;
                                padding: 0 5px;
                                justify-content: center;
                                .img-box {
                                    width: 276px;
                                    height: 276px;
                                    display: flex;
                                    position: relative;
                                   
                                    z-index: 1;
                                    transition: all;
                                    &.on {
                                        animation: sec2Ani2 1.3s ease-in-out both;
                                    }
                                    img{
                                      width: 276px;
                                      height: 276px;
                                      position: relative;
                                      z-index: 1;
                                      transition: all 0.3s;
                                    }
                                }
                                .txt-box {
                                    width: 100%;
                                    position: relative;
                                    display: flex;
                                    &.on {
                                        animation: sec3Ani1 1.3s ease-in-out both;
                                    }
                                    a {
                                        z-index: 3;
                                        position: relative;
                                        right: 0;
                                        bottom: 0;
                                        padding: 10px 20px;
                                        &.on {
                                            animation: sec3Ani4 1.3s ease-in-out both;
                                        }
                                        h2 {
                                            top: 0;
                                            font-size: 18px;
                                           br {
                                            width: 100%;
                                           }
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

            }
        }
    }

    @keyframes sec2Ani1 {
        0% {transform: translateY(1000px); opacity: 0;}
      100% {transform: translateY(0px); opacity: 1;}
    }

    @keyframes sec2Ani2 {
        0% {transform: translateY(1000px); opacity: 0;}
      100% {transform: translateY(0px); opacity: 1;}
    }
    @keyframes sec2Ani3 {
        0% {transform: translateY(1000px);opacity: 0;}
      100% {transform: translateY(0px);opacity: 1;}
    }
